.admin-header {
  position: sticky;
  top: 0px;
  z-index: 3;
  margin-bottom: 0px;
  width: 100%;
  // margin-bottom: 15px;
  background-color: transparent;
  box-shadow: 11px 1px 14px 0px #2330431A;
  border-bottom: none;
  background-color: #FFF;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 30px;

  

  .left {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .app-logo {
      height: 30px;
      cursor: pointer;
    }
  }

  .right {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    .btn-box {
      display: flex;
      justify-content: space-between;
      align-items: center;

      button {
        margin-left: 1.489615rem;
        font-weight: 500;
        font-size: 14px;
        line-height: 1;
        padding: 7px 4px 7px 7px;
        border-radius: 50%;

        .mode i {
          font-size: 21px;
          font-weight: 200;
          -webkit-transition: all 0.3s ease;
          transition: all 0.3s ease;
          color: #676767;
        }
    



        svg {
          margin-left: 0;
          margin-right: 5px;
          height: 30px;
        }
        img {
          margin-left: 0;
          margin-right: 5px;
          height: 30px;
          &.msg-cion{
            height: 25px;
            margin-right: 0;
            padding: 0;
          }
        }
      }
      .notification-badge {
        position: absolute;
        right: 8px;
        top: 5px;
        padding: 2px 4px;
        font-size: 10px;
        color: #fff;
        font-weight: 500;
        font-family: "Poppins", sans-serif;
        background-color: #ff7272;
      }
      .message-badge{
        position: absolute;
        right: 0;
        top: 3px;
        padding: 2px 4px;
        font-size: 10px;
        color: #fff;
        font-weight: 500;
        font-family: "Poppins", sans-serif;
        background-color: #ff7272;
      }


    }
    



    .login-btn-box {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: fit-content;



      button {
        font-size: 14px;
        font-weight: 600;
        padding: 12px 17px;
        border-radius: 50px;
        margin: 0;
        position: relative;
        line-height: 1;
      }

      .login-btn {
        background-color: #0770a8;
        border: none;
        margin-right: 10px;
      }

      .signup-btn {
        background-color: #0770a8;
        border: none;
      }
    }




    .user-box {
      display: flex;
      margin-left: 20px;
      cursor: pointer;
      position: relative;
      .status-img{
        width: 20px;
        height: 20px;
        position: absolute;
        right: 8px;
        bottom: 0;
      }

      .img-box {
        height: 42px;
        width: 42px;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 7px;
      }


    }
  }

   
}

.admin-layout.collapsed-sidebar{
  .admin-header{

  }
}

/* language dropdown */
.dropdown {
  list-style: none;
  margin: 0;
  padding-left: 0;

}



.down-arrow {
  height: 0.177864rem;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-content a {
  float: none;
  color: #191919;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 14px
}

.dropdown-content a:hover {
  background-color: #ddd;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.user-dropdown li p {
  margin: 0 8px 0 11px;
  font-size: 14px;
}

/* language dropdown */






@media (max-width: 1200px) {
  .right button {
    padding: .5rem 1rem;
  }

  .admin-header-btn-box button {
    margin-left: 10px;
    padding: .5rem;
  }
}

@media (max-width: 992px) {
  .search-input-group {
    width: 43vw;
  }

  .admin-header .top {
    display: block;

  }

  .right .header-btn-box button {
    padding: 10px 7px 10px 10px !important;
    margin-right: 10px;
  }

  .right {

    justify-content: space-between;
  }

  .signup-btn {
    margin-left: 10px;
  }

  .user-dropdown {
    width: fit-content;
    margin-top: 10px !important;

  }

  .admin-header .top .right {

    justify-content: space-between;
  }

  .admin-header-btn-box {
    justify-content: start;
  }

  .search-input-group {
    width: 41vw;
  }

}

@media (max-width: 767px) {
  .admin-header {
    position: sticky;
    top: 0;
    background-color: #fff;
    border-bottom: unset;
    z-index: 3;
    margin-bottom: 15px;

  }

  .app-logo {
    height: 30px;
    margin-right: auto;
    margin-left: auto;
  }

  .admin-header .top {
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 5px;
    padding-left: 5px;
  }

  .admin-header .top>div {
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 5px;
    padding-left: 5px;
  }

  .admin-header .top .left {
    margin-bottom: 0px;
  }

  .search-input {

    padding: 9px 9px 9px 9px;
  }

  .header-user-box .img-box {
    height: 35px;
    width: 46px;
  }

  .right button {
    padding: .5rem 1rem;

  }

  .admin-header .bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 15px;

  }

  .bottom .header-btn-box button {
    padding: 2px 7px 2px 10px !important;

  }

  .search-input-group {
    width: 100% !important;
  }

  .admin-header .search-input {
    width: 175px !important;
  }

  .search-input-group .input-group-text {
    border-radius: 10px;
    background-color: #fff;
    padding: 5px;

  }

  .admin-header-user-box {

    margin-left: 0px;

  }

  .mobile-filter-btn {
    box-shadow: none;
    background-color: #325A3E;
    border: none;
    border-radius: 7px;
    padding: 8px 10px
  }

}

