.jvectormap-zoomin,
.jvectormap-zoomout,
.jvectormap-goback {
    position: absolute;
    left: 10px;
    border-radius: 3px;
    background: #292929;
    padding: 5px;
    color: #fff;
    cursor: pointer;
    line-height: 20px;
    text-align: center;
    -webkit-box-sizing: content-box;
    box-sizing: content-box
}

.jvectormap-container {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden
}

.jvector-map-height {
    height: 389px
}

.jvectormap-tip {
    position: absolute;
    display: none;
    border: solid 1px #cdcdcd;
    border-radius: 3px;
    background: #292929;
    color: #fff;
    font-family: sans-serif, Verdana;
    font-size: smaller;
    padding: 3px
}

.jvectormap-zoomin {
    width: 20px;
    height: 20px;
    bottom: 10px
}

.jvectormap-zoomout {
    width: 20px;
    height: 20px;
    bottom: 50px
}

.jvectormap-goback {
    bottom: 10px;
    z-index: 1000;
    padding: 6px
}

.jvectormap-spinner {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: center no-repeat url(data:image/gif;base64,)
}

.jvectormap-legend-title {
    font-weight: 700;
    font-size: 14px;
    text-align: center
}

.jvectormap-legend-cnt {
    position: absolute
}

.jvectormap-legend-cnt-h {
    bottom: 0;
    right: 0
}

.jvectormap-legend-cnt-h .jvectormap-legend {
    float: left;
    margin: 0 10px 10px 0;
    padding: 3px 3px 1px
}

.jvectormap-legend-cnt-h .jvectormap-legend .jvectormap-legend-tick {
    float: left
}

.jvectormap-legend-cnt-h .jvectormap-legend-tick {
    width: 40px
}

.jvectormap-legend-cnt-h .jvectormap-legend-tick-sample {
    height: 15px
}

.jvectormap-legend-cnt-h .jvectormap-legend-tick-text {
    text-align: center
}

.jvectormap-legend-cnt-v {
    top: 0;
    right: 0
}

.jvectormap-legend-cnt-v .jvectormap-legend {
    margin: 10px 10px 0 0;
    padding: 3px
}

.jvectormap-legend-cnt-v .jvectormap-legend-tick-sample {
    height: 20px;
    width: 20px;
    display: inline-block;
    vertical-align: middle
}

.jvectormap-legend-cnt-v .jvectormap-legend-tick-text {
    display: inline-block;
    vertical-align: middle;
    line-height: 20px;
    padding-left: 3px
}

.jvectormap-legend {
    background: #000;
    color: #fff;
    border-radius: 3px
}

.jvectormap-legend-tick-text {
    font-size: 12px
}

.jvectormap-legend .jvectormap-legend-tick-sample {
    height: 26px
}

