.users-permission-page table thead tr th{text-align:center;font-size:13px !important;padding:10px !important;font-size:16px;border-bottom:none !important;color:#0d233e;font-weight:600 !important;text-transform:capitalize;white-space:nowrap}.users-permission-page table tbody tr td{text-align:center;padding:10px !important;font-weight:500 !important}.users-permission-page .custom-checkbox{appearance:none;cursor:pointer;height:16px;width:16px;border-radius:4px;display:inline-block;position:relative;border:1px solid #f2a93e;background-color:rgba(0,0,0,0);transition:all .3s ease-in-out}.users-permission-page .custom-checkbox::before{content:"";font-family:remixicon !important;position:absolute;top:50%;left:50%;color:#f2a93e;transform:translate(-50%, -50%) scale(0);transition:all .3s ease-in-out}.users-permission-page .custom-checkbox:focus{border:1px solid #f2a93e}.users-permission-page .custom-checkbox:disabled{cursor:default;border:1px solid #e7e7e7}.users-permission-page .cancel-btn{border:1px solid #f07054;color:#f07054}