.admin-subscription-tiers-merchant .expiry.input-box{display:flex;align-items:center;gap:10px}.admin-subscription-tiers-merchant .settings-item{padding:10px 5px;border-radius:6px}.admin-subscription-tiers-merchant .settings-item .label{font-weight:500;font-size:calc(14px + 3*(100vw - 320px)/1600);margin-bottom:8px}.admin-subscription-tiers-merchant .settings-item .label .instructions{font-size:calc(11px + 2*(100vw - 320px)/1600);font-weight:400;opacity:.6}.admin-subscription-tiers-merchant .input-container{position:relative;display:flex;align-items:center}.admin-subscription-tiers-merchant .input-container input{width:100%;padding:10px 18px 10px 18px;border:1px solid #ccc;border-radius:12px;outline:none;background-color:inherit;font-weight:500;font-size:15px}.admin-subscription-tiers-merchant .input-container input::placeholder{color:rgba(0,0,0,0)}.admin-subscription-tiers-merchant .input-container .unit{position:absolute;padding:10px 0 10px 18px;font-weight:500;font-size:14px;pointer-events:none;left:50px}.admin-subscription-tiers-merchant .input-container .unit.miles{font-size:14px}.admin-subscription-tiers-merchant .input-container .unit.price{font-size:15px;left:68px}.admin-subscription-tiers-merchant .input-container .unit.time{font-size:14px}.admin-subscription-tiers-merchant .input-container .unit.percent{left:30px}.admin-subscription-tiers-merchant .input-container .input-placeholder{position:absolute;top:6px;left:18px;font-size:12px;color:#aaa}.admin-subscription-tiers-merchant .check-box-container{display:flex;align-items:center;gap:5px;padding:10px 18px 10px 18px;border:1px solid #ccc;border-radius:12px;background-color:#fff}.admin-subscription-tiers-merchant .check-box-container .label{margin:0;font-weight:450;font-size:15px;cursor:pointer}.admin-subscription-tiers-merchant .custom-checkbox{appearance:none;cursor:pointer;height:16px;width:16px;border-radius:4px;display:inline-block;position:relative;border:1px solid rgba(var(--theme-color), 1);background-color:rgba(0,0,0,0);transition:all .3s ease-in-out}.admin-subscription-tiers-merchant .custom-checkbox::before{content:"";font-family:remixicon !important;position:absolute;top:50%;left:50%;color:rgba(var(--theme-color), 1);transform:translate(-50%, -50%) scale(0);transition:all .3s ease-in-out}.admin-subscription-tiers-merchant .custom-checkbox:focus{border:1px solid rgba(var(--theme-color), 1)}.admin-subscription-tiers-merchant .custom-checkbox:disabled{cursor:default;border:1px solid #e7e7e7}.admin-subscription-tiers-merchant .custom-checkbox:checked{background-color:rgba(var(--theme-color), 1);border:1px solid rgba(var(--theme-color), 1)}.admin-subscription-tiers-merchant .change-btn{font-size:calc(13px + 1*(100vw - 320px)/1600);font-weight:500;background-color:rgba(var(--theme-color), 1);padding:10px 8px;color:#fff;align-self:center;border-radius:10px;cursor:pointer;min-width:90px;width:fit-content;display:flex;align-items:center;justify-content:center}